import { useState } from 'react';
import { Helmet } from 'react-helmet';
import { isMobile, isTablet } from 'react-device-detect';
import { titlePageGenerator } from '../../../utils';
import TransitionLayer from '../../atoms/TransitionLayer/TransitionLayer';
import ButtonBanner from '../../molecules/ButtonBanner/ButtonBanner';
import LandingPageMobile from './LandingPageMobile/LandingPageMobile';

function LandingPage() {
  const [isVideoReady, setIsVideoReady] = useState(false);

  if (isMobile || isTablet) {
    return <LandingPageMobile />;
  }

  return (
    <div className="h-screen w-full overflow-hidden">
      <Helmet>
        <title>{titlePageGenerator('Welcome')}</title>
      </Helmet>
      <div className="relative">
        <video
          onLoadedData={() => setIsVideoReady(true)}
          className="z-10 h-screen w-full object-cover"
          autoPlay
          muted
          loop
          src="/videos/visual-gate.mp4"
        >
          <track kind="video gate" />
        </video>
        <ButtonBanner />
        <TransitionLayer showTransition={isVideoReady} />
      </div>
    </div>
  );
}

export default LandingPage;
