import logo from '../assets/ipb-jobfair-logo.png';
import ilustration from '../assets/business-woman.png';
import { BgDecoration } from './BgDecoration';

export default function HeroSection() {
  return (
    <section
      id="hero-section"
      className="relative overflow-hidden"
      style={{
        height: 'calc(100vh - 64px)',
        width: '100%',
        background: 'radial-gradient(100% 100% at 79.07% 100%, #8E2DE2 0%, #2F008F 100%)',
      }}
    >
      <BgDecoration />
      <div className="px-14 pt-20 md:flex md:items-center">
        <div>
          <div className="flex justify-center">
            <img
              src={logo}
              alt="logo"
              style={{ filter: 'drop-shadow(0px 0px 8px rgba(255, 255, 255, 0.5))', width: '96px' }}
            />
          </div>
          <div className="mt-6 mb-2">
            <h2 className="mb-1 text-center text-2xl font-bold text-white">HADIR KEMBALI!</h2>
            <p className="text-center text-sm text-white">
              Ayo dapatkan pekerjaan impianmu dengan mengikuti rangkaian kegiatan dari IPB Job Fair 2022
            </p>
          </div>
        </div>

        <div className="relative flex justify-center">
          <div
            className="absolute inset-0 top-0 left-0 z-10 m-auto aspect-square"
            style={{ width: '182px', background: 'white', opacity: '0.75', filter: 'blur(100px)' }}
          />
          <img src={ilustration} alt="business woman illustration" className="z-20 block w-full" />
        </div>
      </div>
      <p className="mt-10 animate-bounce text-center text-white">
        <span>scroll</span>
        <br />
        <i className="bi bi-arrow-down" />
      </p>
    </section>
  );
}
