import ExpoCardTL from './ExpoCardTL';
import ITACard from './ITACard';
import WSCard from './WSCard';

export default function TimelineSection() {
  return (
    <section
      className="bg-[#F2E8DD] bg-repeat pb-14"
      style={{ backgroundImage: 'url("./img/bg-decoration.png")' }}
    >
      <div className="pt-10 text-center text-xl">
        <p className="font-semibold text-[#272727]">Apa saja rangkaian</p>
        <h2 className="text-2xl font-bold tracking-wide text-jobfairYellow-2">IPB JOB FAIR?</h2>
      </div>
      <p className="mb-10 px-16 text-center text-sm">Ada tiga rangkaian yang bisa kamu ikuti tahun ini</p>
      <div className="grid grid-cols-1 gap-10 px-10 md:grid-cols-3">
        <ITACard />
        <WSCard />
        <ExpoCardTL />
      </div>
    </section>
  );
}
